.popup {
    position: fixed;
    width: 100%;
    height: 100vh;
    left: 0;
    top: 0;
    z-index: 100;
    pointer-events: none;
    transition: all 500ms ease;
    opacity: 0;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__overlay {
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,0.75);
        padding: 2.4rem;
        box-sizing: border-box;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__content {
        box-sizing: border-box;
        position: relative;
        width: min(100%, 48rem);
        height: 48rem;
        border-radius: var(--border-radius-lg);
        background-color: #fff;
        padding: 0;
    }
    &__btn-close {
        width: 3.6rem;
        height: 3.6rem;
        position: absolute;
        right: 1.6rem;
        top: 1.6rem;
        background: var(--color-main-500) url('../images/icon-close-w.svg') no-repeat center center;
        background-size: 2rem;
        font-size: 0;
        border: 0;
        border-radius: 50%;
    }
}