.alert {
    background-color: var(--color-red-500);
    padding: 2rem 2.4rem;
    position: relative;
    & > div {
        font-size: var(--font-size-base);
        line-height: 1.6;
        color: #fff;
        padding-right: 4.8rem;
    }
    & > button {
        width: 4rem;
        height: 4rem;
        position: absolute;
        right: 1.2rem;
        top: 1.2rem;
        transform: translate3d(0,0,0);
        background: transparent url('../images/icon-close-w.svg') no-repeat center center;
        background-size: 2.4rem;
        font-size: 0;
        border: 0;
    }
}