.oops {
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 2.4rem;
    box-sizing: border-box;
    &__content {
        width: clamp(48rem, 100%, 48rem);
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
    }
    &__image {
        width: 8rem;
        margin: 0 auto;
    }
    &__title {
        font-size: var(--font-size-3xl);
        color: var(--color-main-500);
        text-align: center;
    }
    &__text {
        @extend %site-text;
        text-align: center
    }
    &__btn-buy {
        @extend %site-button;
    }
}