.body-type-detail,
.meridian-detail,
.tips-detail {
    &__content {
        box-sizing: border-box;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: min-content;
        gap: 4rem;
        overflow-y: auto;
        height: 100%;
        padding: 4.8rem 2.4rem 3.2rem;
        & > h2 {
            font-size: var(--font-size-xl);
            color: var(--color-main-500);
            text-align: center;
            padding-top: 3.2rem;
        }
        & > ul {
            display: grid;
            grid-template-columns: 1fr;
            gap: 1.2rem;
            list-style: disc;
            padding-left: 1.2rem;
            li {
                @extend %site-text;
            }
        }
        ol {
            display: grid;
            grid-template-columns: 1fr;
            gap: 0.8rem;
            list-style: decimal;
            padding-left: 1.2rem;
            li {
                @extend %site-text;
            }
        }
    }
}

.tips-detail {
    &__section {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;
        p {
            @extend %site-text;
        }
    }
    &__list {
        display: grid;
        grid-template-columns: 1fr;
    }
    &__item {
        display: flex;
        gap: 0.8rem;
        align-items: center;
        padding: 1.6rem 0;
        &:not(:last-child) {
            border-bottom: 0.1rem solid var(--color-gray-75);
        }
        span {
            &:nth-child(1) {
                padding: 0.8rem;
                border-radius: 50%;
                color: #fff;
                font-size: var(--font-size-sm);
            }
            &:nth-child(2) {
                font-size: var(--font-size-sm);
                color: var(--color-gray-500);
            }
        }
        &.red {
            span {
                &:nth-child(1) {
                    background-color: var(--color-red-500);
                }
            }
        }
        &.orange {
            span {
                &:nth-child(1) {
                    background-color: var(--color-orange-500);
                }
            }
        }
        &.yellow {
            span {
                &:nth-child(1) {
                    background-color: var(--color-yellow-500);
                }
            }
        }
        &.green {
            span {
                &:nth-child(1) {
                    background-color: var(--color-green-500);
                }
            }
        }
        &.wind {
            span {
                &:nth-child(1) {
                    background-color: var(--color-main-500);
                }
            }
        }
    }
}