.measure-info {
    display: grid;
    grid-template-columns: 1fr;
    gap: 1.2rem;
    position: relative;
    &__gender,
    &__date,
    &__remark {
        display: flex;
        gap: 1.2rem;
        span {
            font-size: var(--font-size-base);
            font-weight: 500;
            &:nth-child(1) {
                color: var(--color-gray-300);
            }
            &:nth-child(2) {
                font-weight: 700;
                color: var(--color-gray-500);
            }
        }
    }
    &__btn-share,
    &__btn-tips {
        width: 3.6rem;
        height: 3.6rem;
        position: absolute;
        right: 0;
        top: 0;
        border-radius: 50%;
        border: 0;
        font-size: 0;
    }
    &__btn-share {
        background: var(--color-main-500) url('../images/icon-share.svg') no-repeat center center;
        background-size: 2rem;
    }
    &__btn-tips {
        background: var(--color-main-500) url('../images/icon-tips.svg') no-repeat center center;
        background-size: 2rem;
    }
}