.three {
    &__frame {
        position: relative;
        height: 100%;
        overflow: hidden;
        width: 100%;
        z-index: 0;
        img {
            width: auto;
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate3d(-50%,-50%,0);
        }
    }
    &__control {
        position: absolute;
        left: 50%;
        transform: translate3d(-50%,0,0);
        bottom: 4rem;
        display: flex;
        gap: 1.2rem;
    }
    &__btn-prev,
    &__btn-tips,
    &__btn-next {
        width: 4rem;
        height: 4rem;
        border-radius: var(--border-radius-base);
        background-repeat: no-repeat;
        background-color: var(--color-main-500);
        background-position: 50%;
        display: block;
        padding: 0;
        border: 0;
        font-size: 0;
        &.disabled {
            opacity: 0.5;
            pointer-events: none;
            background-color: var(--color-gray-500);
        }
    }
    &__btn-prev {
        background-image: url('../images/icon-prev.svg');
    }
    &__btn-next {
        background-image: url('../images/icon-next.svg');
    }
    &__btn-tips {
        background-image: url('../images/icon-tips.svg');
    }
}