:root {
    font-size: 10px;
    --color-bg: #F7F7F7;
    --color-main-700: #016E7A;
    --color-main-500: #00B4A4;
    --color-green-800: #357F12;
    --color-green-500: #72C14D;
    --color-yellow-800: #c69b05;
    --color-yellow-500: #EFBC08;
    --color-orange-500: #D1712C;
    --color-orange-800: #ae5617;
    --color-red-500: #D94D4E;
    --color-red-800: #a20f10;
    --color-blue-500: #3EA4DE;
    --color-meridian-0: #9ED6DE;
    --color-meridian-1: #97BBDD;
    --color-meridian-2: #8F99DB;
    --color-meridian-3: #AD8EDB;
    --color-meridian-4: #D190DB;
    --color-meridian-5: #D490A6;
    --color-meridian-6: #D6A290;
    --color-meridian-7: #DABB92;
    --color-gray-700: #413E3E;
    --color-gray-500: #5F5A5A;
    --color-gray-400: #797575;
    --color-gray-300: #939090;
    --color-gray-200: #ADAAAA;
    --color-gray-100: #C7C5C5;
    --color-gray-75: #EBEBEB;
    --color-gray-50: #F7F7F7;
    --font-size-7xl: 9.6rem;
    --font-size-6xl: 6.4rem;
    --font-size-5xl: 4.8rem;
    --font-size-4xl: 3.6rem;
    --font-size-3xl: 3rem;
    --font-size-2xl: 2.4rem;
    --font-size-xl: 2rem;
    --font-size-lg: 1.8rem;
    --font-size-base: 1.6rem;
    --font-size-sm: 1.4rem;
    --font-size-xs: 1.2rem;
    --border-radius-lg: 1.2rem;
    --border-radius-base: 0.8rem;
    --border-radius-sm: 0.4rem;
    --color-type-0: #8DD8E0;
    --color-type-1: #8DBDE0;
    --color-type-2: #8D9AE0;
    --color-type-3: #B38DE0;
    --color-type-4: #DD8DE0;
    --color-type-5: #E08DA7;
    --color-type-6: #E0A08D;
    --color-type-7: #E0BA8D;
    --color-type-8: #E0D08D;
    --color-type-9: #CDE08D;
    --color-type-10: #A3E08D;
    --color-type-11: #8DE09D;
    --color-type-12: #8DE0D0;
    --color-type-13: #999999;
}

html {
    font-family: 'Roboto', 'Noto Sans TC', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    &.fixed {
        overflow: hidden;
    }
}

body {
    background-color: var(--color-bg);
    &.fixed {
        overflow: hidden;
    }
}

.wrap {
    box-sizing: border-box;
    max-width: 48rem;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    gap: 2rem;
    padding: 2.4rem;
    &.three {
        max-width: inherit;
        display: block;
        padding: 0;
        width: 100%;
        height: 100vh;
    }
}

.block-ui {
    border-radius: var(--border-radius-lg);
    border: solid 0.1rem var(--color-gray-75);
    background-color: #fff;
    &__header {
        border-bottom: solid 0.1rem var(--color-gray-75);
        display: flex;
        padding: 0 2.4rem;
        height: 6.4rem;
        align-items: center;
        justify-content: space-between;
        position: relative;
        h2 {
            font-size: var(--font-size-xl);
            color: var(--color-gray-500);
        }
    }
    &__btn-more {
        background-color: var(--color-main-500);
        font-size: var(--font-size-sm);
        color: #fff;
        border-radius: var(--border-radius-base);
        padding: 0.8rem 1.2rem;
        border: 0;
        font-weight: 700;
    }
    &__body {
        padding: 2rem 2.4rem;
    }
    &__switch {
        display: flex;
        border-radius: var(--border-radius-base);
        overflow: hidden;
    }
    &__switch-btn {
        border: 0;
        font-weight: 700;
        background-color: var(--color-gray-500);
        padding: 0.8rem 2.4rem;
        color: #fff;
        font-size: var(--font-size-base);
        transition: all 300ms ease;
        &.active {
            pointer-events: none;
            background-color: var(--color-main-500);
        }
    }
    &__btn-tips {
        width: 3.6rem;
        height: 3.6rem;
        position: absolute;
        right: 2rem;
        top: 50%;
        border-radius: 50%;
        border: 0;
        font-size: 0;
        background: var(--color-main-500) url('../images/icon-tips.svg') no-repeat center center;
        background-size: 2rem;
        transform: translate3d(0,-50%,0)
    }
}

.btn-full-report {
    @extend %site-button;
}