.measure-nav {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 2rem;
    &__btn {
        padding: 1.6rem;
        border-radius: var(--border-radius-lg);
        background-color: var(--color-main-500);
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 0.8rem;
        img {
            width: 2.4rem;
        }
        span {
            color: #fff;
            font-weight: 700;
            font-size: var(--font-size-lg);
        }
    }
}